#hero {
    width: 100%;
    height: 100vh;
    &:before {
        content: '';
        background: rgba(5, 13, 24, .7);
        position: absolute;
        bottom: 0;
        top: 0;
        left: 0;
        right: 0;
        z-index: 1; }
    .hero-container {
        position: relative;
        z-index: 2;
        min-width: 300px; }
    h1 {
        margin: 0 0 10px 0;
        font-size: 64px;
        font-weight: 700;
        line-height: 56px;
        color: #fff; }
    p {
        color: #fff;
        margin-bottom: 50px;
        font-size: 26px;
        .typed {
            margin-left: 10px;
            color: #149ddd; } } }

