.portfolio {
    .portfolio-item {
        margin-bottom: 30px; }
    #portfolio-filters {
        padding: 0;
        margin: 0 auto 35px auto;
        list-style: none;
        text-align: center;
        background: #fff;
        border-radius: 50px;
        padding: 2px 15px;
        li {
            cursor: pointer;
            display: inline-block;
            padding: 10px 15px 8px 15px;
            font-size: 14px;
            font-weight: 600;
            line-height: 1;
            text-transform: uppercase;
            color: #272829;
            margin-bottom: 5px;
            transition: all 0.3s ease-in-out;
            &:hover {
                color: #149ddd; }
            &:last-child {
                margin-right: 0; } }
        .filter-active {
            color: #149ddd; }
        .filter-nonactive {
            color: #272829; } }
    .portfolio-wrap {
        transition: 0.3s;
        position: relative;
        overflow: hidden;
        z-index: 1;
        &:hover::before {
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            opacity: 1; }
        &:hover {
            .portfolio-links {
                opacity: 1;
                bottom: 0; } }
        &::before {
            content: "";
            background: rgba(255, 255, 255, 0.5);
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            transition: all ease-in-out 0.3s;
            z-index: 2;
            opacity: 0; }
        .portfolio-links {
            opacity: 1;
            left: 0;
            right: 0;
            bottom: -60px;
            z-index: 3;
            position: absolute;
            transition: all ease-in-out 0.3s;
            display: flex;
            justify-content: center;
            a {
                color: #fff;
                font-size: 28px;
                text-align: center;
                background: rgba(20, 157, 221, 0.75);
                transition: 0.3s;
                width: 100%;
                &:hover {
                    background: rgba(20, 157, 221, 0.95); } }
            a + a {
                border-left: 1px solid #37b3ed; } } } }
.item-enter {
    opacity: 0; }

.item-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-out; }

.item-exit {
    opacity: 1; }

.item-exit-active {
    opacity: 0;
    transition: opacity 500ms ease-out; }


