.contact {
    .info {
        padding: 30px;
        background: #fff;
        width: 100%;
        box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.12);
        i {
            font-size: 20px;
            color: #149ddd;
            float: left;
            width: 44px;
            height: 44px;
            background: #dff3fc;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50px; }
        h4 {
            padding: 0 0 0 60px;
            font-size: 22px;
            font-weight: 600;
            margin-bottom: 5px;
            color: #050d18; }
        p {
            padding: 0 0 10px 60px;
            margin-bottom: 20px;
            font-size: 14px;
            color: #173b6c; }
        .email {
            &:hover {
                i {
                    background: #149ddd;
                    color: #fff; } }
            p {
                padding-top: 5px; } }
        .address:hover {
            i {
                background: #149ddd;
                color: #fff; } }
        .phone:hover {
            i {
                background: #149ddd;
                color: #fff; } }
        .social-links {
            padding-left: 60px;
            a {
                font-size: 18px;
                display: inline-block;
                background: #333;
                color: #fff;
                line-height: 1;
                padding: 8px 0;
                border-radius: 50%;
                text-align: center;
                width: 36px;
                height: 36px;
                margin-right: 10px;
                &:hover {
                    background: #149ddd;
                    color: #fff; } } } }
    .email-form {
        width: 100%;
        padding: 30px;
        background: #fff;
        box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.12);
        input {
            border-radius: 0;
            box-shadow: none;
            font-size: 14px;
            height: 44px; }
        textarea {
            border-radius: 0;
            box-shadow: none;
            font-size: 14px;
            padding: 10px 12px; }
        .form-group {
            padding-bottom: 8px; }
        .validate {
            display: block;
            color: red;
            margin: 0 0 15px 0;
            font-weight: 400;
            font-size: 13px; }
        .sending {
            display: block;
            background: #fff;
            text-align: center;
            padding: 15px;
            border-radius: 4px;
            &::before {
                content: "";
                display: inline-block;
                border-radius: 50%;
                width: 24px;
                height: 24px;
                margin: 0 10px -6px 0;
                border: 3px solid #18d26e;
                border-top-color: #eee;
                -webkit-animation: animate-loading 1s linear infinite;
                animation: animate-loading 1s linear infinite; } }
        .error-message {
            display: block;
            color: #fff;
            background: #ed3c0d;
            text-align: center;
            padding: 15px;
            font-weight: 600;
            border-radius: 4px; }
        .sent-message {
            display: block;
            color: #fff;
            background: #18d26e;
            text-align: center;
            padding: 15px;
            font-weight: 600;
            border-radius: 4px; }
        button {
            background: #149ddd;
            border: 0;
            padding: 10px 24px;
            color: #fff;
            border-radius: 4px;
            cursor: pointer;
            &:hover {
                background: #37b3ed; } } } }
