body {
  font-family: "Open Sans", sans-serif;
  color: #272829; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif; }

#main {
  margin-left: 300px; }

section {
  padding: 25px 0;
  overflow: hidden; }

.section-bg {
  background: #f5f8fd; }

.section-title {
  padding-bottom: 30px;
  h2 {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 20px;
    padding-bottom: 20px;
    position: relative;
    color: #173b6c;
    &::after {
      content: '';
      position: absolute;
      display: block;
      width: 50px;
      height: 3px;
      background: #149ddd;
      bottom: 0;
      left: 0; } }
  p {
    margin-bottom: 0; } }
